.info{
  width: 50%;
  margin: 0 auto;
  @media screen and(max-width: 1360px){
    width: 75%;
  }
  @media screen and(max-width: 1024px){
    width: 100%;
    padding: 0 20px;
  }
  &-list{
    display: grid;
    grid-template-columns: 28% calc( 100% - 28% - 30px);
    grid-column-gap: 30px;
    @media screen and(max-width: 800px){
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 600px){
      grid-template-columns: repeat(1, 1fr);
    }
    &--half{
      grid-template-columns: repeat(2, 1fr);
      @media screen and(max-width: 600px){
        grid-template-columns: repeat(1, 1fr);
        grid-row-gap: 20px;
      }
    }
    &--ambasador{
      margin-top: 20px;
    }
    &__item{
      width: 50%;
      @media screen and(max-width: 800px){
        width: 100%;
      }
    }
  }
  &-loader{
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;  
    padding-bottom: 40px;
    border-bottom: 1px dotted #f0f0f0;
  }
  &-pasport{
    margin-top: 20px;
  }
  &-ambasador{
    margin-top: 45px;
    &__title{
      color: #242323;
      font-family: 'SFUIText-Bold';
      font-size: 20px;
      font-weight: 700;
      line-height: 20px;
      margin-bottom: 17px;
    }
    &__text{
      padding: 12px 0;
      @media screen and(max-width: 600px){
        padding: 0;
        text-align: center;
      }
      &-name{
        color: #242323;
        font-size: 16px;
        font-weight: 400;
        line-height: 15px;
        margin-bottom: 8px;
      }
      &-title{
        color: #242323;
        font-family: 'SFUIText-Bold';
        font-size: 18px;
        font-weight: 700;
        line-height: 20px;
      }
    }
  }
  &-btn{
    margin-top: 60px;
    text-align: center;
  }
  &-politic{
    margin-top: 40px;
    color: #242323;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    line-height: 20px;
    span{
      color: #ef443a;
      font-weight: 500;
      text-decoration: underline
    };
  }
}