@font-face {
  font-family: 'SFProDisplay-Regular';
  src: url('../fonts/SFProDisplay-Regular.woff') format('woff'),
    url('../fonts/SFProDisplay-Regular.eot') format('embedded-opentype'),
    url('../fonts/SFProDisplay-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SFUIText-Bold';
  src: url('../fonts/SFUIText-Bold.woff') format('woff'),
    url('../fonts/SFUIText-Bold.eot') format('embedded-opentype'),
    url('../fonts/SFUIText-Bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}