.btn {
  width: 255px;
  height: 70px;
  border-radius: 40px;
  font-size: 18px;
  font-weight: 700;
  line-height: 15px;
  outline: none;
  border:none;
  padding: 0 35px;
  cursor: pointer;
  @media screen and (max-width: 900px){
    width: 200px;
  }
  @media screen and (max-width: 540px){
    width: 145px;
  }
  &-primary {
    background-color: #ef443a;
    color: #ffffff;
  }
  &-banner {
    width: 319px;
    height: 80px;
    background-color: transparent;
    color: #ffffff;
    border: 1px solid #ffffff;
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    justify-content:  space-around;
    align-items: center;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
    @media screen and (max-width: 480px){
      width: 95%;
    }
    &-primary {
      background-color: #ef443a;
      color: #ffffff;
      border: none;
      margin-right: 20px;
    }
  }
  &-secondary {
    border: 1px solid #d8d8d8;
    background-color: #ffffff;
    color: #242323;
  }
}