.banner {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 250px;
  display: flex;
  flex-direction: column;
  background-image: url(../../../assets/img/banner.png);
  background-size: cover;
  @media screen and (max-width: 900px){
    padding-top: 250px;
  }
  @media screen and (max-width: 480px){
    padding-top: 155px;
    padding-bottom: 0;
    background-size: 300%;
  }
  @media screen and (max-width: 380px){
    background-size: 340%;
    padding-top: 130px;
  }
  &__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60%;
    margin: 145px auto auto auto;
    @media screen and (max-width: 1000px){
      margin-top: 0;
      width: 90%;
    }
    &-title {
      color: #ffffff;
      font-size: 45px;
      font-weight: 300;
      line-height: 55px;
      text-align: center;
      margin-bottom: 30px;
      margin-top:0;
      @media screen and (max-width: 540px){
        font-size: 30px;
        line-height: 1.4;
        margin-bottom: 0;
      }
    }
    &-subtitle {
      color: #ffffff;
      font-size: 30px;
      font-weight: 300;
      line-height: 40px;
      margin-bottom: 61px;
      margin-top:0;
      text-align: center;
      @media screen and (max-width: 540px){
        font-size: 20px;
        margin-bottom: 10px;
      }
    }
  }
  &__counter {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    margin-right: 100px;
    @media screen and (max-width: 480px){
      justify-content: center;
      margin: 0 auto;
    }
    &-text {
      color: #ffffff;
      font-size: 15px;
      font-weight: 500;
      line-height: 20px;
      text-transform: uppercase;
      text-align: center;
      width: 27%;
    }
  }
  &__buttons  {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:76%;
    @media screen and (max-width: 900px){
      justify-content: space-between;
      width: 100%;
    }
    @media screen and (max-width: 740px){
      flex-direction: column;
      width: 100%;
      align-items: center;
    }
  }
}