.description{
  width: 50%;
  margin: 0 auto;
  @media screen and (max-width: 1200px){
    width: 70%;
  }
  @media screen and (max-width: 768px){
    width: 90%;
  }
  &-btn{
    margin-top: 60px;
    text-align: center;
    button{
      margin-right: 0;
      &:nth-child(1){
        border: 1px solid #d8d8d8;
        color: #242323;
        background: white;
        margin-right: 20px;
        
      }
    }
  }
}