.how-to-get {
  width: 50%;
  @media screen and (max-width: 1200px){
    width: 70%;
  }
  @media screen and (max-width: 768px){
    width: 90%;
  }
  margin: 0 auto;
  &-title {
    color: #242323;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    text-align: center;
  }
  &__switch {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 45px;
    color: #242323;
    font-size: 14px;
    font-weight: 400;
    line-height: 15px;
  }
  &-btn{
    margin-top: 60px;
    text-align: center;
    button{
      margin-right: 0;
      &:nth-child(1){
        border: 1px solid #d8d8d8;
        color: #242323;
        background: white;
        margin-right: 20px;
        
      }
    }
  }
}