.success {
  width: 60%;
  margin: 60px auto 0 auto;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  @media screen and (max-width: 768px){
    width: 80%;
  }
  &-title {
    color: #242323;
    font-size: 40px;
    font-weight: 700;
    line-height: 50px;
    @media screen and (max-width: 768px){
      font-size: 30px;
    }
    @media screen and (max-width: 480px){
      font-size: 25px;
    }
  }
  &-subtitle {
    color: #242323;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    @media screen and (max-width: 768px){
      font-size: 16px;
    }
  }
}