.progress{
  display: flex;
  width: 348px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  @media screen and (max-width: 600px){
    width: 300px;
  }
  &-step{
    color: #242323;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    text-align: center;
  }
  &-item{
    width: 14px;
    height: 14px;
    border: 1px solid #d8d8d8;
    border-radius: 50%;
    &--active{
      width: 30px;
      height: 30px;
      background-color: #ef443a;
      border: none;
    }
    &--past{
      border: none;
      background-color: #ef443a;
    }
  }
}