.field{
  width: 100%;
  margin-bottom: 20px;
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__title{
      color: #242323;
      font-size: 20px;
      font-weight: 700;
      line-height: 20px;
      margin-bottom: 17px;
    }
    &__count{
      color: #a1a1a1;
      font-size: 13px;
      font-weight: 400;
      line-height: 20px;
      display: flex;
      margin-bottom: 17px;
    }
    &__warning{
      margin-left: 20px;
      width: 20px;
      height: 20px;
      background-color: #ef443a;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      color: #ffffff;
      font-size: 13px;
      font-weight: 400;
      line-height: 20px;
    }
  }
  &-input{
    border: 1px solid #d8d8d8;
    width: 100%;
    // height: 100%;
    border-radius: 5px;    
    color: #242323;
    font-size: 18px;
    font-weight: 400;
    padding: 24px 30px;
    outline:none;
    &::first-letter{
      text-transform: uppercase;
    }
    &:focus{
      border: 1px solid #242323;
    }
    &:placeholder{
      color: #a1a1a1;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
    }
  }
  &-error{
    color: red;
    margin-top: 5px;
  }
}