.dropdown{
  width: 100%;
  height: 70px;
  border-radius: 5px;
  border: 1px solid #d8d8d8;
  padding: 25px 30px;
  display: flex;
  justify-content: space-between;
  &--focus{
    border-radius: 5px 5px 0 0;
    border: 1px solid #242323;
    background-color: #ffffff;
    outline: none;
  }
  &-title{
    color: #242323;
    font-family: 'SFUIText-Bold';
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
    margin-bottom: 17px;
  }
  &-name{
    color: #242323;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px
  }
  &-icon{
    transition: .3s;
    &__path{
      fill: #242323;
    }
    &--rotate{
      transform: rotate(180deg);
      transition: .3s;
    }
  }
  &-wrapper{
    width: 100%;
    outline: none;
    position: relative;
    &--shadow{
      box-shadow: 0 30px 50px rgba(36, 35, 35, 0.1);
    }
  }
  &-submenu{
    padding: 0 20px 20px 20px;
    height: 240px;
    box-shadow: 0 30px 50px rgba(36, 35, 35, 0.1);
    border-radius: 0 0 5px 5px;
    border: 1px solid #242323;
    border-top: none;
    background-color: #ffffff;
    position: absolute;
    z-index: 230;
    width: 100%;
    overflow: hidden;
    overflow-y: auto;
    &__list{
      padding: 0;
      margin: 0;
      list-style: none;
      margin-top: 30px;
      padding-left: 10px;
    }
    &__item{
      margin-bottom: 35px;
      color: #242323;
      font-family: 'SFUIText-Bold';
      font-size: 16px;
      font-weight: 600;
      line-height: 1;
      cursor: pointer;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  &-search{
    position: relative;
    &__input{
      width: 100%;
      border: none;
      border-bottom: 1px dotted #d8d8d8;
      padding: 20px 28px 19px 10px;

      outline: none;
      &:placeholder{
        color: #a1a1a1;
        font-family: "SF Pro Display";
        font-size: 16px;
        font-weight: 400;
        line-height: 20px
      }
    }
    &__icon{
      position: absolute;
      right: 0px;
      top: 20px;

    }
  }
}