.checkbox-black {
  &__label{
    border-radius: 22px;
    border: 1px solid #d8d8d8;
    padding: 10px 17px;
    color: #242323;
    font-size: 14px;
    font-weight: 500;
    line-height: 15px;
    display: flex;
    width: fit-content;
    align-items: center;
    span{
      margin-right: 12px;
      .hunt-svg{
        fill: #a1a1a1;
      }
    }
  }
  &__input{
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
  }
}

.checkbox-black__input:checked ~ .checkbox-black__label{
  background-color: #242323;
  color: #ffffff;
  background-color: #242323;
  border: 1px solid #242323;
}
.checkbox-black__input:checked ~ label span .hunt-svg{
  fill: #fff;
}