.api{
  width: 100%;
  margin-bottom: 20px;
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    // margin-bottom: 17px;
    &__title{
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: #242323;
      font-size: 20px;
      font-weight: 700;
      line-height: 20px;
      p {
        margin-left: 10px;
      }
    }
    &__warning{
      margin-left: 20px;
      width: 20px;
      height: 20px;
      background-color: #ef443a;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      color: #ffffff;
      font-size: 13px;
      font-weight: 400;
      line-height: 20px;
    }
  }
  &-input{
    border: 1px solid #d8d8d8;
    width: 100%;
    border-radius: 5px;    
    color: #242323;
    font-size: 18px;
    font-weight: 400;
    padding: 25px 25px 25px 50px;
    outline:none;
    background: url(../../../assets/img/location.png) no-repeat 20px 27px;
    background-size: auto 20%;
    &:focus{
      border: 1px solid #eee8e8;
    }
    &:placeholder{
      color: #a1a1a1;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
    }
  }
}