.animals{
  border-radius: 10px;
  border: 1px dashed #d8d8d8;
  padding: 40px;
  margin-bottom: 40px;
  &-wrapper{
    width: 790px;
    margin: 0 auto;
    @media screen and(max-width: 1360px){
      width: 75%;
    }
    @media screen and(max-width: 1024px){
      width: 100%;
      padding: 0 20px;
    }
  }
  &-title{
    color: #242323;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
    &--margin{
      margin-bottom: 12px;
      margin-top: 40px;
    }
  }
  &-list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 55px;
    grid-row-gap: 20px;
    @media screen and(max-width: 768px){
      grid-template-columns: repeat(1, 1fr);
    }
    &__count{
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media screen and(max-width: 600px){
        flex-direction: column;
        align-items: flex-start;
      } 
      .wrap{
        @media screen and(max-width: 768px){
          width: 200px;
        }         
      }
      .field{
        margin-bottom: 0;
      }
      &-text{
        color: #242323;
        font-size: 14px;
        font-weight: 400;
        line-height: 16px;
        width: 110px;
        @media screen and(max-width: 768px){
          width: 100%;
        }
        @media screen and(max-width: 600px){
          margin-bottom: 5px;
        } 
        &--width{
          width: 120px;
          @media screen and(max-width: 768px){
            width: 100%;
          }
        }
      }
      &-wrap{
        .field{
          margin-bottom: 0;
        }
      }
      .field{
        width: 150px;
        @media screen and(max-width: 768px){
          width: 200px;
        }         
      }
      .wrap{
        margin-bottom: 0;
      }
    }
    &__calendar{
      display: flex;
      align-items: center;
      &-line{
        padding: 0 12px;
        color:#242323; 
      }
      &-title{
        margin-bottom: 8px;
        color: #242323;
        font-size: 14px;
        font-weight: 400;
        line-height: 15px;
        &--opacity{
          opacity: 0;
        }
      }
    }
  }
  &-btn{
    margin-top: 60px;
    text-align: center;
    button{
      margin-right: 0;
      &:nth-child(1){
        border: 1px solid #d8d8d8;
        color: #242323;
        background: white;
        margin-right: 20px;
        
      }
    }
  }
  &-delete{
    margin-top: 20px;
    margin-left: auto;
    color: #ef443a;
    font-size: 18px;
    font-weight: 700;
    line-height: 20px;
    text-align: right;
    cursor: pointer;
  }
  &-add{
    cursor: pointer;
    margin-left: 40px;
    display: flex;
    align-items: center;
    &__icon{
      width: 40px;
      height: 40px;
      border-radius: 22px;
      border: 1px solid #d8d8d8;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__text{
      margin-left: 20px;
      color: #242323;
      font-size: 18px;
      font-weight: 700;
      line-height: 20px;
    }
  }
}

.react-datepicker__input-container input {
  border: 1px solid #d8d8d8;
  width: 100%;
  border-radius: 5px;
  font-size: 18px;
  font-weight: 400;
  padding: 24px 20px;
  outline: none;
  color: #242323;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
}
.react-datepicker-popper[data-placement^="bottom"]{
  margin-top: 0;
  margin-bottom: 18px;
}
.react-datepicker__triangle{
  display: none;
}
.react-datepicker{
  box-shadow: 0 30px 50px rgba(36, 35, 35, 0.1);
  border-radius: 0 0 5px 5px;
  border: 1px solid #242323;
  background-color: #ffffff;
}
.react-datepicker__header{
  padding-top: 20px;
  background: white;
  border: none;
  &:first-letter{
    text-transform: uppercase;
  }
}
.react-datepicker__current-month{
  color: #242323;
  font-family: 'SFUIText-Bold';
  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
  margin-bottom: 22px;
}
.react-datepicker__navigation {
  top: 25px;
}
.react-datepicker__navigation--previous {
  background: url(../../../assets/img/arrow_left.png) no-repeat;
  width: 8px;
  height: 12px;
  border: none;
  left: 28px;
}
.react-datepicker__navigation--next {
  background: url(../../../assets/img/arrow_right.png) no-repeat;
  width: 8px;
  height: 12px;
  border: none;
  right: 28px;
}
// .react-datepicker__month-container{
//   width: 330px;
// }
.react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name{
  width: 2.45rem;
}
.react-datepicker__day-name{
  font-family: 'SFUIText-Bold';
  color: #242323;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
}
.react-datepicker__day{
  color: #d8d8d8;
  font-size: 14px;
  font-weight: 600;
}

.react-datepicker__day-names, .react-datepicker__week{
  margin-bottom: 15px;
}

.react-datepicker__day--selected{
  border-radius: 30px;
  background-color: #242323;
  line-height: 36px;
}
.react-datepicker__day--in-range{
  border-radius: 30px;
  background-color: #242323;
  line-height: 36px;
}
.react-datepicker__month--selected-range .react-datepicker__day--in-range{
  background: red;
  color: blue;
}
.react-datepicker__day:hover, .react-datepicker__month-text:hover{
  // line-height: 36px;
  border-radius: 30px;
}
.react-datepicker__day--in-selecting-range{
  background: #f0f0f0; 
}
.react-datepicker__day--in-selecting-range:hover{
  background-color: #242323;
  color: #d8d8d8;
}
.react-datepicker__day{
  line-height: 36px;
}

.checkbox-list{
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-gap: 10px;
  @media screen and(max-width: 600px){
    grid-template-columns: repeat(1, auto);
  } 
  &--flex{
    display: flex;
    flex-wrap: wrap;
    .checkbox-black{
      margin-right: 10px;
      margin-bottom: 10px;
    }
  }
}