.food{
  border: 1px dashed #d8d8d8;
  padding: 40px;
  border-radius: 10px;
  margin-bottom: 40px;
  &-wrapper{
    width: 790px;
    margin: 0 auto;
  }
  &-list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
  &-delete{
    margin-top: 20px;
    margin-left: auto;
    color: #ef443a;
    font-size: 18px;
    font-weight: 700;
    line-height: 20px;
    text-align: right;
    cursor: pointer;
  }
  &-add{
    cursor: pointer;
    margin-left: 40px;
    display: flex;
    align-items: center;
    &__icon{
      width: 40px;
      height: 40px;
      border-radius: 22px;
      border: 1px solid #d8d8d8;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__text{
      margin-left: 20px;
      color: #242323;
      font-size: 18px;
      font-weight: 700;
      line-height: 20px;
    }
  }
  &-btn{
    margin-top: 60px;
    text-align: center;
    button{
      margin-right: 0;
      &:nth-child(1){
        border: 1px solid #d8d8d8;
        color: #242323;
        background: white;
        margin-right: 20px;
        
      }
    }
  }
}