.hunting-details {
  width: 50%;
  @media screen and (max-width: 1300px){
    width: 70%;
  }
  @media screen and (max-width: 1100px){
    width: 90%;
  }
  margin: 0 auto;
  &-title {
    color: #242323;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    text-align: center;
  }
  &__error{
    display: flex;
  }
  &__count {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
    @media screen and (max-width: 850px){
      flex-direction: column;
      align-items: center;
    }
    &__item {
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      justify-content: space-between;
      width: 47%;
      @media screen and (max-width: 850px){
        width: 100%;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
      }
    }
  }
  &-btn{
    margin-top: 60px;
    text-align: center;
    button{
      margin-right: 0;
    }
  }
}
.error-text{
  color:red;
  width: 47%;
  &--right{
    margin-left: auto;
  }
}