.form{
  &-group{
    margin-bottom: 40px;
  }
  &-label{
    color: #242323;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
    margin-bottom: 17px;
  }
  &-control{
    height: 180px;
    border-radius: 5px;
    border: 1px solid #d8d8d8;
    resize: none;
    width: 100%;
    outline: none;
    padding: 20px 30px;
    &:placeholder{
      color: #a1a1a1;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
    }
  }
}