.footer {
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  max-width: 100%;
  height: 191px;
  padding: 0 100px;
  background-color: #242323;
  color: hsl(0, 0%, 100%);
  @media screen and (max-width: 1024px){
    padding: 20px;
  }
  @media screen and (max-width: 768px){
    padding: 20px;
    height: auto;
  }
  &__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    @media screen and (max-width: 1360px){
      flex-wrap: wrap
    }
    @media screen and (max-width: 768px){
      justify-content: center;
    }
    // @media screen and (max-width: 560px){
    //   width: 100%;
    // }
  }
  &__menu{
    display: flex;
    align-items: center;
    @media screen and (max-width: 768px){
      flex-direction: column;
      width: 100%;
    }
    span{
      color: #ffffff;
      padding: 0 5px;
      border-right: 1px solid white;
      font-size: 12px;
      font-weight: 400;
      line-height: 14px;
      text-transform: uppercase;
      a{
        text-decoration: none;
        color: #ffffff;
      }
      @media screen and (max-width: 768px){
        border: none;
        padding: 10px 0;
      }
      &:last-child{
        border: none;
      }
    }
  }
  &__info{
    display: flex;
    align-items: center;
    @media screen and (max-width: 1360px){
      width: 50%;
    }
    @media screen and (max-width: 1300px){
      margin-top: 20px;
    }
    @media screen and (max-width: 1024px){
      width: 70%;
    }
    @media screen and (max-width: 768px){
      width: 100%;
      flex-wrap: wrap;
      margin-top: 0px;
    }
    &-item{
      display: flex;
      align-items: center;
      @media screen and (max-width: 768px){
        width: 100%;
        justify-content: center;
        margin-top: 10px;
      }
      span{
        color: #ffffff;
        margin-left: 15px;
        margin-right: 35px;
        font-size: 12px;
        font-weight: 500;
        line-height: 20px;
        text-transform: uppercase;
      }
    }
  }
  &__icons {
    display: flex;
    flex-direction: row;
    align-items: center;
    @media screen and (max-width: 1360px){
      margin-top: 20px;
      justify-content: space-between;
      width: 50%;
    }
    @media screen and (max-width: 1024px){
      width: 30%;
    }
    @media screen and (max-width: 768px){
      width: 100%;
      justify-content: center;
      margin-top: 10px;
    }
    a{
      margin-right: 15px;
      &:last-child{
        margin: 0;
      }
    }
    // width: 60%;
    // @media screen and (max-width: 1200px){
    //   width:70%;
    // }
    // @media screen and (max-width: 600px){
    //   width: 100%;
    // }
  }

}