.wrap{
  width: 150px;
  margin-bottom: 20px;
  &-title{
    color: #242323;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
    margin-bottom: 17px;
    width: 200%;
  }
  &-label {
    color: #242323;
    font-size: 14px;
    font-weight: 400;
    line-height: 15px;
  }
  &-count{
    border: 1px solid #d8d8d8;
    width: 100%;
    border-radius: 5px;    
    color: #242323;
    font-size: 18px;
    font-weight: 400;
    padding: 17px 20px;
    margin-top: 8px;
    outline:none;
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &-button {
      outline: none;
      border: none;
      background-color: transparent;
    }
    &-add{
      &:disabled{
        svg{
          path{
            fill:#d8d8d8;
          }
        }
      }  
    }
    &-minus {
      padding-bottom: 13px;
      &:disabled{
        svg{
          path{
            fill:#d8d8d8;
          }
        }
      }
    }
  }
}