.accommodation {
  width: 790px;
  margin: 0 auto;
  @media screen and (max-width: 850px){
    width: 90%;
  }
  &-title {
    color: #242323;
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    text-align: center;
  }
  &__block {
    width: 100%;
    border-radius: 10px;
    border: 1px dashed #838080;
    padding: 40px;
    margin-bottom: 40px;
    &__row {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      @media screen and (max-width: 850px){
        flex-direction: column;
      }
      &-type {
        width: 48%;
        @media screen and (max-width: 850px){
          width: 90%;
          margin-bottom: 20px;
        }
      }
      &-item {
        width: 20%;
        @media screen and (max-width: 850px){
          width: 40%;
        }
        @media screen and (max-width: 480px){
          width: 70%;
        }
      }
    }
    &__features {
      display:grid;
      grid-template-columns: repeat(7, auto);
      grid-row-gap: 15px;
      @media screen and (max-width: 850px){
        grid-template-columns: repeat(4, auto);
      }
      @media screen and (max-width: 500px){
        grid-template-columns: repeat(2, auto);
      }
      &-title {
        color: #242323;
        font-size: 20px;
        font-weight: 700;
        line-height: 20px;
        margin-bottom: 17px;
      }
    }
    &__switch {
      display: flex;
      flex-direction: row;
      width: 45%;
      justify-content: space-between;
      align-items: center;
      color: #242323;
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      padding-top: 40px;
      @media screen and (max-width: 850px){
        width: 90%;
      }
      &__column {
        display: flex;
        flex-direction: column;
        p {
          margin-bottom: 0;
        }
      }
      span {
        color: #a1a1a1;
      }
    }
    &-delete {
      color: #ef443a;
      font-size: 18px;
      font-weight: 700;
      line-height: 20px;
      text-align: right;
    }
  }
  &__add{
    cursor: pointer;
    margin-left: 40px;
    display: flex;
    align-items: center;
    margin: 40px auto 60px 40px;
    &-icon{
      width: 40px;
      height: 40px;
      border-radius: 22px;
      border: 1px solid #d8d8d8;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &-text{
      margin-left: 20px;
      color: #242323;
      font-size: 18px;
      font-weight: 700;
      line-height: 20px;
    }
  }
  &__btn{
    margin-top: 60px;
    text-align: center;
    button{
      margin-right: 0;
      cursor: pointer;
      outline: none;
      margin-bottom: 20px;
      &:nth-child(1){
        border: 1px solid #d8d8d8;
        color: #242323;
        background: white;
        margin-right: 20px;
      }
    }
  }
}

.disabled-block{
  opacity: 0.5;
  pointer-events: none;
}