.rectangle {
  min-width: 99px;
  min-height: 75px;
  border-radius: 10px;
  border: 1px dashed #ffffff;
  @media screen and (max-width: 1100px){
    min-width: 80px;
    min-height: 65px;
  }
  @media screen and (max-width: 540px){
    min-width: 60px;
    min-height: 45px;
  }
  @media screen and (max-width: 400px){
    min-width: 40px;
    min-height: 20px;
  }
  &-wrapper {
    display: flex;
    flex-direction: row;
  }
  &-t {
    color: #ffffff;
    font-size: 30px;
    font-weight: 500;
    line-height: 20px;
    margin: 7% 11px;
  }
  &-number {
    color: #ef443a;
    font-size: 40px;
    font-weight: 700;
    line-height: 45px;
    margin-bottom: 0;
    margin-top: 15px;
    text-align: center;
    @media screen and (max-width: 1100px){
      font-size: 33px;
      margin-top: 10px;
    }
    @media screen and (max-width: 540px){
      font-size: 25px;
      margin-top: 0;
    }
    @media screen and (max-width: 400px){
      font-size: 18px;
    }
  }
  &-title {
    color: #ffffff;
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
    text-align: center
  }
}