.checkbox{
  display: flex;
  align-items: center;
  &-input{
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    &:checked ~ .checkbox-custom:before{
      content: '\2714';
      width: 12px;
      height: 19px;
      display: flex;
      margin: auto;
      color: #ef443a;
    }
  }
  &-custom{
    width: 20px;
    height: 20px;
    border-radius: 5px;
    border: 1px solid #d8d8d8;
    background-color: #f0f0f0;
    display: block;
  }
  &-title{
    color: #242323;
    margin-left: 15px;
    font-size: 16px;
    font-weight: 400;
    line-height: 15px;
  }
}

