.loader{
  position: relative;
  &-title{
    color: #242323;
    font-size: 16px;
    font-weight: 400;
    line-height: 15px;
    margin-bottom: 18px;
  }
  &-input{
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
  &-close{
    margin: 0 30px;
    width: 20px;
    height: 20px;
    background-color: #ffffff;
    border-radius:50%;
    position: absolute;
    bottom: -10px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 200;
  }
  &-label{
    position: relative;
    width: 80px;
    height: 80px;
    border-radius: 5px;
    border: 1px dashed #d8d8d8;
    display: block;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 1;
    margin: 0 5px;
    &--border{
      border: none;
      display: none;
    }
  }
  &-image{ 
    width: 80px;
    height: 80px;
    border-radius: 5px;
  }
}



