.loader-list {
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  @media screen and (max-width: 850px){
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-row-gap: 15px;
  }
  @media screen and (max-width: 500px){
    grid-template-columns: repeat(2, auto);
  }
  &-title {
    color: #242323;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
  }
  &-subtitle {
    color: #a1a1a1;
    font-size: 16px;
    font-weight: 400;
    line-height: 15px;
  }
}