.offer {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  &-title {
    color: #242323;
    font-size: 45px;
    font-weight: 300;
    line-height: 55px;
    text-align: center;
  }
  &__item {
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 150px;
    @media screen and (max-width: 768px){
      flex-direction: column;
    }
    img {
      @media screen and (max-width: 1275px){
        width: 40%;
      }
      @media screen and (max-width: 768px){
        width:100%;
      }
    }
    &__reverse {
      flex-direction: row-reverse;
      @media screen and (max-width: 768px){
        flex-direction: column;
      }
    }
    &__content {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      width: 50%;
      @media screen and (max-width: 768px){
        width:90%;
      }
      &__reverse {
        margin-left: 270px;
        img {
          width:46px;
          height:42px;
        }
        @media screen and (max-width: 1275px){
          margin-left: 30px;
          margin-top: 30px;
        }
      }
      &-title {
        color: #242323;
        width: 70%;
        margin: 0px;
        color: #242323;
        font-size: 48px;
        font-weight: 300;
        line-height: 48px;
        span{
          color: #ef443a;
        }
        @media screen and (max-width: 480px){
          font-size: 25px;
        }
        &--red{
          color: #ef443a;
        }
        &-reverse {
          width: 80%;
          span{
            color: #ef443a;
          }
        }
      }
      &-text {
        color: #242323;
        font-size: 20px;
        font-weight: 300;
        line-height: 30px;
        width: 69%;
        margin: 28px 0 30px 0;
        @media screen and (max-width: 480px){
          font-size: 15px;
        }
        &-reverse {
          width: 79%;

        }
      }
      &-button {
        text-align: left;
        border-radius: 40px;
        border: none;
        background-color: transparent;
        outline: none;
        color: #242323;
        padding: 0;
        font-size: 16px;
        font-weight: 700;
        line-height: 20px;
        span{
          margin-right: 8px;
        }
      }
    }
  }
}